* {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: large;
}

/* Estilo para centrar todo el contenido del body */
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0;
}


/* Estilo para el div con clase "form_element" */
.form_element {
  display: inline-block; /* Para que se ajuste al contenido */
  text-align: left; /* Para alinear el contenido al centro */
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 2rem; /* Reduce el relleno para ajustarse al contenido */
  margin: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Estilo para divs anidados dentro de .form_element */
.form_element div {
  text-align: left; /* Alinea el contenido de divs anidados al centro */
}

/* Estilo para el input de tipo texto y número */
input[type="text"],
input[type="number"] {
  width: 30rem;
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: #f9f9f9;
  color: #333;
  transition: border-color 0.3s, box-shadow 0.3s;
}

/* Cambia el estilo del input cuando se enfoca (focus) */
input[type="text"]:focus,
input[type="number"]:focus {
  border-color: #007bff; /* Puedes personalizar el color de foco */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Estilo para el placeholder del input */
input::placeholder {
  color: #999;
}

/* Estilo para el input cuando está deshabilitado */
input:disabled {
  background-color: #eee;
  cursor: not-allowed;
}


/* Estilo para el botón */
button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

/* Cambia el estilo del botón cuando se pasa el mouse por encima (hover) */
button:hover {
  background-color: #0056b3; /* Personaliza el color de fondo en hover */
}

/* Cambia el estilo del botón cuando se hace clic (active) */
button:active {
  background-color: #004387; /* Personaliza el color de fondo en clic */
}

/* Cambia el estilo del botón cuando está deshabilitado */
button:disabled {
  background-color: #ccc;
  color: #999;
  cursor: not-allowed;
}

/* Estilo para el texto dentro del botón */
button span {
  font-weight: bold;
}



/* Estilo para el contenedor con clase "boolean_instance" */
.boolean_instance {
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: fit-content;
  padding: 1em;
  margin: 1em;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Estilo para la etiqueta label dentro del contenedor */
.boolean_instance label {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
}

/* Estilo para el párrafo dentro de la etiqueta label */
.boolean_instance label p {
  font-weight: bold;
  margin: 0;
  color: #333;
}

/* Estilo para el input de tipo checkbox dentro de la etiqueta label */
.boolean_instance label input[type="checkbox"] {
  width: 20px;
  height: 20px;
  appearance: none;
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, border-color 0.3s;
}

/* Cambia el estilo del checkbox cuando está marcado */
.boolean_instance label input[type="checkbox"]:checked {
  background-color: #007bff; /* Cambia el color de fondo cuando está marcado */
  border-color: #007bff; /* Cambia el color del borde cuando está marcado */
}


/* Estilo para el elemento select */
select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: #f9f9f9;
  color: #333;
  transition: border-color 0.3s, box-shadow 0.3s;
}

/* Cambia el estilo del select cuando se enfoca (focus) */
select:focus {
  border-color: #007bff; /* Personaliza el color de enfoque */
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Estilo para las opciones del select */
select option {
  font-size: 16px;
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px 10px;
}

/* Cambia el estilo de las opciones cuando se pasan el mouse por encima (hover) */
select option:hover {
  background-color: #007bff; /* Personaliza el color de fondo en hover */
  color: #fff;
}
